 import config from '../config/config';
 // Replace with your client id
async function redirectToAuthCodeFlow() {
  const verifier = generateCodeVerifier(128);
  const challenge = await generateCodeChallenge(verifier);

  localStorage.setItem("verifier", verifier);

  const params = new URLSearchParams();
  params.append("client_id", config.clientId);
  params.append("response_type", "code");
  // params.append("redirect_uri", "https://spotify-metrics.netlify.app/callback");
  params.append("redirect_uri", "http://localhost:3000/callback");
  params.append("scope", "user-read-private user-read-email user-top-read playlist-modify-public user-modify-playback-state user-read-playback-state user-follow-read user-read-recently-played");
  params.append("code_challenge_method", "S256");
  params.append("code_challenge", challenge);

  document.location = `https://accounts.spotify.com/authorize?${params.toString()}`;
}

async function getAccessToken(clientId: string, code: string): Promise<string> {
  const verifier = localStorage.getItem("verifier");
  const params = new URLSearchParams();
  params.append("client_id", clientId);
  params.append("grant_type", "authorization_code");
  params.append("code", code);
  params.append("redirect_uri", "http://localhost:3000/callback");
  params.append("code_verifier", verifier!);

  const result = await fetch("https://accounts.spotify.com/api/token", {
    method: "POST",
    headers: { "Content-Type": "application/x-www-form-urlencoded" },
    body: params,
  });

  const { access_token } = await result.json();
  return access_token;
}

async function fetchProfile(token: string): Promise<any> {
  const response = await fetch("https://api.spotify.com/v1/me", {
      method: "GET", headers: { Authorization: `Bearer ${token}` }
  });
  const data = await response.json();
  console.log("Fetched profile data:", data); // Logging to check the profile data

  // Ensure the profile structure matches what your interface expects
  // Update the mapping if necessary
  return {
    displayName: data.display_name,
    avatarUrl: data.images[0]?.url || "", // Using the first image URL
    id: data.id,
    email: data.email,
    uri: data.uri,
    url: data.external_urls.spotify,
    imgUrl: data.images[0]?.url || "",
  };
}
function generateCodeVerifier(length: number) {
  let text = '';
  let possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';

  for (let i = 0; i < length; i++) {
      text += possible.charAt(Math.floor(Math.random() * possible.length));
  }
  return text;
}

async function generateCodeChallenge(codeVerifier: string) {
  const data = new TextEncoder().encode(codeVerifier);
  const digest = await window.crypto.subtle.digest("SHA-256", data);
  return btoa(
    String.fromCharCode.apply(null, Array.from(new Uint8Array(digest)))
  )
    .replace(/\+/g, "-")
    .replace(/\//g, "_")
    .replace(/=+$/, "");
}

export {
  redirectToAuthCodeFlow,
  getAccessToken,
  fetchProfile,
}
